import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMapMarkerAlt, faTimes, faStar, faHeart } from '@fortawesome/free-solid-svg-icons'
import { Swipeable } from 'react-swipeable'

import './App.scss';
import profiles from './data/profiles.json';

class App extends React.Component {
        constructor(props) {
            super(props);

            this.drag = React.createRef();
            this.overlay = React.createRef();
            this.times = React.createRef();
            this.heart = React.createRef();
            this.star = React.createRef();
            this.state = {
                killer: profiles[0],
                pos: 0
            };

            this.animate = this.animate.bind(this);
            this.onSwipeLeft = this.onSwipeLeft.bind(this);
            this.onSwipeRight = this.onSwipeRight.bind(this);
            this.onStarring = this.onStarring.bind(this);
        }

        changeProfile() {
            

            var id = this.state.pos;
            while (id === this.state.pos) {
                id = Math.floor(Math.random() * (profiles.length));
            }

            this.setState({
                pos: id,
                killer: profiles[id]
            });

            const card = document.getElementById("swiper");
            card.classList.add('animated', "bounceIn", "fast");

            function handleAnimationEnd() {
                card.classList.remove('animated', "bounceIn", "fast")
                card.removeEventListener('animationend', handleAnimationEnd)
            }

            card.addEventListener('animationend', handleAnimationEnd);
        }

        animate(animation) {
            const card = document.getElementById("swiper");
            card.classList.add('animated', animation, "slow");
            
            var self = this;
            function handleAnimationEnd() {
                card.classList.remove('animated', animation, "slow")
                card.removeEventListener('animationend', handleAnimationEnd)

                self.overlay.current.classList.remove("visible", "red", "green", "blue");
                self.times.current.classList.remove("visible");
                self.heart.current.classList.remove("visible");
                self.star.current.classList.remove("visible");
                self.changeProfile();
            }

            card.addEventListener('animationend', handleAnimationEnd);
        }

        onSwipeLeft() {
            this.overlay.current.classList.add("visible", "red");
            this.times.current.classList.add("visible");
            this.animate("bounceOutLeft");
        }

        onSwipeRight() {
            this.overlay.current.classList.add("visible", "green");
            this.heart.current.classList.add("visible");
            this.animate("bounceOutRight");
        }

        onStarring() {
            this.overlay.current.classList.add("visible", "blue");
            this.star.current.classList.add("visible");
            this.animate("bounceOutRight");
        }

        onSwipeUp() {
            document.getElementById("desc").classList.add("swiped-up");
            document.getElementById("image").classList.add("min");
            document.getElementById("profile-description").classList.add("maxed");
        }

        onSwipedDown() {
            document.getElementById("desc").classList.remove("swiped-up");
            document.getElementById("image").classList.remove("min");
            document.getElementById("profile-description").classList.remove("maxed");
        }

  render() {
    return (
        <Swipeable onSwipedLeft={this.onSwipeLeft} onSwipedRight={this.onSwipeRight}  onSwipedUp={this.onSwipeUp} onSwipedDown={this.onSwipedDown} preventDefaultTouchmoveEvent={true} >
                <div id="swiper" className="card">
                    <div className="card-header">
                        <div id="profile-image" className="row">
                            <div className="column">
                                <img id="image" src={ "/dead-by-tinder/images/" + this.state.killer.id + ".png" } alt={ this.props.killer } />
                                <div ref={ this.overlay } className="image-overlay">
                                    <div ref={ this.times } className="overlay-text"><FontAwesomeIcon icon={faTimes} /></div>
                                    <div ref={ this.heart } className="overlay-text"><FontAwesomeIcon icon={faHeart} /></div>
                                    <div ref={ this.star } className="overlay-text"><FontAwesomeIcon icon={faStar} /></div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div id="desc" className="card-content">
                        <div id="profile" className="row">
                            <div id="profile-name" className="row">
                                <div className="column">
                                    <span id="name">{this.state.killer.name}</span>
                                </div>
                            </div>

                            <div id="profile-location" className="row">
                                <div className="column">
                                    <FontAwesomeIcon icon={faMapMarkerAlt} />
                                    <span id="location">{this.state.killer.location}</span>
                                </div>
                            </div>

                            <div id="profile-description" className="row">
                                <div id="profile-long-description">
                                    <p>{this.state.killer.description}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div id="actions" className="row">
                    <div className="column column">
                        <span onMouseUp={this.onSwipeLeft}><FontAwesomeIcon icon={faTimes} /></span>
                        <span onMouseUp={this.onStarring}><FontAwesomeIcon icon={faStar} /></span>
                        <span onMouseUp={this.onSwipeRight}><FontAwesomeIcon icon={faHeart} /></span>
                    </div>
                </div>
        </Swipeable>
        );
    }
}

export default App;
